import { useEffect, useState } from 'react';
import { UserListApi } from '../../utils/adminApi';
import { Table, Typography } from 'antd';
const { Column } = Table;

const Status = () => {
  const [tableData, setTableData] = useState();

  useEffect(() => {
    UserListApi().then((res) => {
      setTableData(res.data.map((item) =>({...item, _id: item._id.slice(0,7),  createdAt: item.createdAt.replaceAll(/[TZ]/g,' '), updatedAt: item.updatedAt.replaceAll(/[TZ]/g,' ')}) ));
    });
  }, []);

  const ban = () => {};
  const edit = () => {};
  const recharge = () => {};
  const del = () => {};

  return (
    <Table dataSource={tableData} rowKey="_id" >
      <Column title="序号" dataIndex="_id"  />
      <Column title="会员编号" dataIndex="nick"  />
      <Column title="完成截图" dataIndex="freeze" render={(_,record) => <img className='w-24 h-24 bg-red-400' />} />
      <Column title="奖励金币" dataIndex="blance"  />
      <Column title="状态" dataIndex="state"  />
      <Column
        title="操作"
        dataIndex=""
        render={(_, record) => (
					<div className='flex gap-1' >
          <Typography.Link onClick={() => ban(record)}>封号</Typography.Link>
          <Typography.Link onClick={() => edit(record)}>修改</Typography.Link>
          <Typography.Link onClick={() => recharge(record)}>充值</Typography.Link>
          <Typography.Link onClick={() => del(record)}>删除</Typography.Link>
					</div>
        )}
      />
    </Table>
  );
};

export default Status;
